













import Vue from 'vue';

export default Vue.extend({
  name: 'v-button',
  props: {
    busy: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isDisabled(): boolean {
      return this.busy || this.disabled;
    },
  },
  methods: {
    onClick(event: Event) {
      this.$emit('click', event);
    },
  },
});
