






















import Vue from 'vue';

interface ChildComponent {
  isActive: boolean;
}

export default Vue.extend({
  name: 'v-tabs',
  data() {
    return {
      tabs: [] as ChildComponent[],
    };
  },
  methods: {
    setActive(tab: ChildComponent) {
      this.tabs.forEach((x) => {
        x.isActive = x === tab;
      });
    },
  },
});
