




















import Vue from 'vue';
import { PageEvent } from '@vue-toolkit/common';

export default Vue.extend({
  data() {
    return {
      length: 500,
      pageSize: 50,
      output: null as PageEvent | null,
    };
  },
  methods: {
    onPage(event: PageEvent) {
      this.output = event;
    },
  },
});
