
























import Vue, { VNode } from 'vue';

import Cell from './Cell.vue';
import Header from './Header.vue';

export default Vue.extend({
  name: 'v-table',
  components: {
    'v-cell': Cell,
    'v-header': Header,
  },
  props: {
    data: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      columns: [] as VNode[],
    };
  },
  mounted() {
    if (this.$slots.default) {
      this.columns = this.$slots.default;
    }
  },
});
