
















































import Vue from 'vue';

export default Vue.extend({
  data() {
    return {
      isOpen: false,
      showBackdrop: true,
      showClose: true,
      backdropDismiss: true,
      keyboardDismiss: true,
      size: '',
    };
  },
  methods: {
    onOpen() {
      this.isOpen = true;
    },
    onDismiss() {
      this.isOpen = false;
    },
  },
});
