














import Vue from 'vue';
import { Auth, CognitoUser } from '@aws-amplify/auth';

export default Vue.extend({
  name: 'app-auth-bar',
  data() {
    return {
      user: null as CognitoUser | null,
    };
  },
  created() {
    this.loadUser();
  },
  methods: {
    async loadUser() {
      try {
        this.user = await Auth.currentAuthenticatedUser();
      } catch (error) {
        // not authenticated
        this.user = null;
      }
    },
    async onSignOut() {
      await Auth.signOut();
    },
  },
  computed: {
    isAuthenticated(): boolean {
      return !!this.user;
    },
  },
});
