











import Vue from 'vue';

import { getUniqueId } from '../../utils';

export default Vue.extend({
  name: 'v-field',
  props: {
    label: String,
  },
  data() {
    return {
      id: getUniqueId('control-'),
    };
  },
  mounted() {
    // todo: improve
    const control = this.$children.find((x) => x.$attrs.name != null);

    if (control?.$el) {
      control.$el.id = this.id;
    }
  },
});
