

















































import Vue from 'vue';
import { FormSubmitEvent } from '@vue-toolkit/forms';
import { required, minLength, maxLength } from '@vue-toolkit/forms/validators';

export default Vue.extend({
  data() {
    return {
      value: null as Record<string, unknown> | null,
      submitted: null as FormSubmitEvent | null,
      schema: {
        name: [required, minLength(3), maxLength(20)],
        type: [required],
        priority: [required],
      },
    };
  },
  methods: {
    loadAva() {
      this.value = {
        name: 'Ava',
        description: null,
        type: 'dog',
        priority: 'high',
        isAwesome: true,
      };
    },
    loadLucy() {
      this.value = {
        name: 'Lucy',
        description: null,
        type: 'cat',
        priority: 'low',
        isAwesome: false,
      };
    },
    onSubmit(event: FormSubmitEvent) {
      this.submitted = event;
    },
  },
});
